<template>
  <v-row justify="center" class="my-4">
    <v-col cols="12" md="8">

        <v-card class="pa-6" v-if="!isDigits && !recoveryPass">
            <v-card-title>Recuperación de contraseña</v-card-title>
            <div class="text-subtitle-1 my-4">
                Ingrese a continuación su email para recuperar su contraseña. Le envíaremos un correo.
            </div>
            <v-row class="my-4">
                <v-icon left>mail</v-icon>
                <v-text-field label="email" :rules="emailRules" v-model="email" @keyup.enter="sendEmail"></v-text-field>
            </v-row>
            <v-btn color="primary" block :loading="sendLoading" @click="sendEmail">Enviar</v-btn>
        </v-card>
        <v-card class="pa-6" v-if="isDigits"> 
            <v-form v-model="digitFormValue">
                <v-row justify="center">
                    <v-col cols="12" md="6">
                        <v-otp-input
                            length="6"
                            v-model="digits"
                            :rules="[value => !!value || 'Este campo no pude estar vacío']"
                        ></v-otp-input>
                    </v-col>
                    <v-col cols="12" xs="12">
                        <v-btn :disabled="!digitFormValue" color="primary" block :loading="sendLoading" @click="sendDigits">Enviar</v-btn>
                    </v-col>
                </v-row>           
            </v-form>
        </v-card>

        <v-card class="pa-6" v-if="recoveryPass"> 
            <v-form v-model="recoveryPassFormValue">
                <v-row class="my-4">
                    <v-icon left>lock</v-icon>
                    <v-text-field label="nueva contraseña" type="password" v-model="pass"></v-text-field>
                </v-row> 
                <v-row class="my-4">
                    <v-icon left>lock</v-icon>
                    <v-text-field label="confirmar contraseña" type="password" v-model="passConfirm" :rules="passwordRules" @keyup.enter="changePassword"></v-text-field>
                </v-row>
                <v-btn block color="success" :disabled="!recoveryPassFormValue" :loading="sendLoading" @click="changePassword">Cambiar Contraseña</v-btn>           
            </v-form>
        </v-card>

        <v-alert v-if="alertSuccess" type="success" class="my-4">
            Se envío un correo de comprobación a continuación digite los numeros enviados.
        </v-alert>
        <v-alert v-if="alertError" type="error" class="my-4">
            La cuenta que usted quiere recuperar no existe en el sistema.
        </v-alert>
        <v-alert v-if="alertNotRecovery" type="error" class="my-4">
            Codigo erroneo intente otra vez.
        </v-alert>
    </v-col>
  </v-row>
</template>

<script>
import validator from 'validator'
import userProvider from '@/providers/UserProvider'
import { mapMutations } from 'vuex'
export default {
    name: 'HelpAccountView',
    data(){
        return{
            digitFormValue: false,
            recoveryPassFormValue: false,
            recoveryPass: false,
            email: '',
            pass: '',
            passConfirm: '',
            alertSuccess: false,
            alertError: false,
            alertNotRecovery: false,
            sendLoading: false,
            isDigits: false,
            digits: '',
            token: ''
        }
    },
    computed:{
        emailRules(){
          return [
            value => !!value || 'Este campo no puede estar vacío.',
            value => validator.isEmail(value) || 'Debe contener un email valido.',
          ]
        },
        passwordRules(){
          return [
            value => this.pass === value || 'Las contraseñas deben ser iguales.'
          ]
        },
    },
    methods:{
        ...mapMutations(['setAlert']),
        async sendEmail(){
            this.sendLoading = true
            try {
                const {data: {op: result}} = await userProvider.recoverPass(this.email)
                if(result){
                    this.alertSuccess = true
                    this.alertError = false
                    this.isDigits = true
                }
                else{
                    this.alertError = true
                    this.alertSuccess = false
                }
            } catch (error){
                this.alertError = true
                this.alertSuccess = false
            }
            this.sendLoading = false
        },
        async sendDigits(){
            this.sendLoading = true
            try {
                const {data} = await userProvider.recoverPassToken(this.email, this.digits)
                if(data){
                    this.recoveryPass = true
                    this.isDigits = false
                    this.alertNotRecovery = false
                    this.alertError = false
                    this.alertSuccess = false
                    this.token = data.token
                }
                else{
                    this.recoveryPass = false
                    this.alertNotRecovery = true
                    this.alertError = false
                    this.alertSuccess = false
                }
            } catch (error){
                this.alertNotRecovery = true
                this.alertError = false
                this.alertSuccess = false
            }
            this.sendLoading = false
        },
        async changePassword(){
            this.sendLoading = true
            try {
                await userProvider.changePassword(this.token, this.email, this.pass)
                this.setAlert({
                    state: true,
                    text: 'Se modificó la contraseña exitosamente.',
                    type: 'success'
                })
                this.$router.push('/login')
            } catch (error){
                console.log(error)
                this.setAlert({
                    state: true,
                    text: 'Ocurrió un error al querer modificar la contraseña.',
                    type: 'error'
                })
            }
            this.sendLoading = false
        }
    }
}
</script>

