<template>
  <div class="my-10">
    <v-row justify="center">
        <v-col xs="12" sm="11" md="10" lg="9" xl="8">
            <v-card class="mx-auto pa-4" ref="clickOutSide">
                <v-row class="justify-center align-center">
                    <v-col cols="12" sm="12" md="6">
                        <v-combobox :items="categories" hint="Categoría seleccionada" no-filter label="categoría" v-model="category" @change="fetchProducts(true)"></v-combobox>
                    </v-col>
                    <v-col cols="12" sm="12" md="6">
                        <v-text-field label="buscar" v-model="search" v-on:keyup.enter="fetchProducts(true)"></v-text-field>
                    </v-col>
                </v-row>
                <v-row class="justify-center align-center">
                    <v-col cols="12" sm="12" md="12" lg="12" xl="12" v-for="product of products" v-bind:key="product.title">
                        <v-card class="pa-4" color="blue-grey darken-4">
                            <v-row align="center" justify="center">
                                <v-col cols="12" xs="12" sm="12" md="4" lg="4" xl="4">
                                    <v-row justify="center">
                                        <v-img v-if="$vuetify.breakpoint.smAndDown" aspect-ratio="1.2" :src="product.image"></v-img>
                                        <v-img v-else aspect-ratio="1.2" max-width="200" :src="product.image"></v-img>
                                    </v-row>
                                </v-col>
                                <v-col cols="12" xs="12" sm="12" md="4" lg="4" xl="4">
                                    <div class="text-h4">{{product.title}}</div>
                                    <div class="my-2 text-subtitle-2" >@escarbatopetshop</div>
                                    <div class="my-2 text-subtitle-1" >{{product.description}}</div>
                                    <div class="my-2 text-h5 success--text" >$ {{product.price}}</div>
                                </v-col>
                                <v-col cols="12" xs="12" sm="12" md="4" lg="4" xl="4">
                                    <v-row>
                                        <v-col v-if="profileUsername" cols="12" sm="12" md="12" lg="12" xl="12">
                                            <v-btn
                                            color="primary"
                                            class="white--text"
                                            @click="addToCart(product)"
                                            block
                                            >
                                            Añadir al carrito
                                            <v-icon
                                                right
                                                dark
                                            >
                                            add_shopping_cart
                                            </v-icon>
                                            </v-btn>
                                            <v-spacer></v-spacer>
                                        </v-col>
                                        <v-col v-if="profileUsername" cols="12" sm="12" md="12" lg="12" xl="12">
                                            <v-btn
                                            color="success"
                                            class="white--text"
                                            @click="consultWhatsapp(product.title, product.price)"
                                            block
                                            >
                                            whatsapp
                                            <v-icon
                                                right
                                                dark
                                            >
                                            monetization_on
                                            </v-icon>
                                            </v-btn>
                                        </v-col>
                                        <v-col v-if="!profileUsername" cols="12" sm="12" md="12" lg="12" xl="12">
                                            <v-btn
                                            color="success"
                                            class="white--text"
                                            @click="consultWhatsapp(product.title, product.price)"
                                            block
                                            >
                                            whatsapp
                                            <v-icon
                                                right
                                                dark
                                            >
                                            monetization_on
                                            </v-icon>
                                            </v-btn>
                                        </v-col>
                                    </v-row>
                                </v-col>
                            </v-row>
                            
                            <v-row justify="center">
                                
                            </v-row>
                                
                        </v-card>
                    </v-col>
                </v-row>
                <v-row>
                    <v-btn v-if="page>1" plain @click="changePage(page-1)">{{'< página anterior'}}</v-btn>
                    <v-spacer></v-spacer>            
                    <v-btn v-if="existProxPage" plain @click="changePage(page+1)">{{'próxima página >'}}</v-btn>
                </v-row>
            </v-card>
        </v-col>
    </v-row>
  </div>
</template>

<script>
import { mapMutations } from 'vuex'
import productProvider from '../providers/ProductProvider'
export default {
    data(){
        return{
            categories: [],
            products: [],
            search: '',
            category: 'TODOS',
            page: 1,
            existProxPage: false,
            focusCategory: true,
        }
    },
    computed:{
        profileUsername(){
            if(this.$store.state.userModule.user.username && this.$store.state.userModule.user.username.length>0) return this.$store.state.userModule.user.username.toUpperCase()
            else return false
        },
    },
    async created(){
        this.setLoading(true)
        const {data: {getCategories}} = await productProvider.fetchCategories()

        this.categories = getCategories

        this.fetchProducts()
        this.setLoading(false)
    },
    mounted(){
        this.fetchProducts()
    },
    watch:{
        category(value){
            console.log(value)
            setTimeout(() => {
                
                console.log(this.$refs.clickOutSide)
            }, 2000);
        },
        products(value){
            if(value.length == 0){
                setTimeout(() => {
                    this.category = 'TODOS'
                    this.search = ''
                    this.fetchProducts()
                    this.setAlert({
                    state: true,
                    text: 'Redireccionado a tienda principal',
                    type: 'success'
                    })
                }, 1000);
                this.setAlert({
                    state: true,
                    text: 'No se encontraron productos',
                    type: 'error'
                })
            }
        }
        },
    methods:{
        ...mapMutations(['setAlert', 'addShop', 'setLoading']),
        addToCart(product){
            this.addShop(product)
            this.setAlert({
                state:true,
                text: 'Se ha añadido un nuevo producto a tu carrito',
                type: 'success'
            })
        },
        consultWhatsapp(title, price){
            window.open(`whatsapp://send?text=Buenas!! Quería hacerte consultar por el producto ${title} con un valor de $${price}...&phone=+5491153326667`)
        },
        changePage(page){
            this.page = page
            this.fetchProducts()
            this.$vuetify.goTo(0)
        },
        async fetchProducts(search){
            this.setLoading(true)
            if(search) this.page = 1
            try{
                if(this.$vuetify.breakpoint.name == "xs" || this.$vuetify.breakpoint.name == "sm" || this.$vuetify.breakpoint.name == "md"){
                    const {data: {getFilterProducts: products}} = await productProvider.fetchProductsFilter(this.category, this.search, 0, 0)
                    this.products = products
                }
                else{
                    const {data: {getFilterProducts: products}} = await productProvider.fetchProductsFilter(this.category, this.search, this.page, 10)
                    this.products = products
                    const {data: {getFilterProducts: proxProducts}} = await productProvider.fetchProductsFilter(this.category, this.search, this.page + 1, 10)
                    if(proxProducts.length==0) this.existProxPage = false
                    else this.existProxPage = true
                }
            }catch(error){
                this.setAlert({
                    state: true,
                    text: 'Ocurrió un problema al cargar los productos.',
                    type: 'error'
                })
            }   
            this.setLoading(false)
        }
    }
}
</script>

