<template>
    <div>   
        <v-row class="my-10 mx-4">
            <v-col col="12" sm="12">
                <v-card class="pa-4" v-if="$vuetify.breakpoint.mdAndUp">
                    <v-row>
                        <v-col cols="12" md="1">
                            <v-card-text class="text-center">IMAGEN</v-card-text>
                        </v-col>
                        <v-col cols="12" md="2">
                            <v-card-text class="text-center">NOMBRE DEL PRODUCTO</v-card-text>
                        </v-col>
                        <v-col cols="12" md="2">
                            <v-card-text class="text-center">PRECIO DEL PRODUCTO</v-card-text>
                        </v-col>
                        <v-col cols="12" md="2">
                            <v-card-text class="text-center">DESCRIPCIÓN</v-card-text>
                        </v-col>
                        <v-col cols="12" md="2">
                            <v-card-text class="text-center">CATEGORÍA</v-card-text>
                        </v-col>
                        <v-col cols="12" md="2">
                            <v-card-text class="text-center">UNIDADES</v-card-text>
                        </v-col>
                        <v-col cols="12" md="1">
                            <v-card-text class="text-center">ACCIONES</v-card-text>
                        </v-col>
                    </v-row>
                </v-card>
                <v-card class="pa-4" v-else-if="cart.length>0 && $vuetify.breakpoint.smAndDown">
                    <v-row class="align-center">
                        <v-col cols="4" xs="4">
                            <v-card-text class="text-center">TOTAL</v-card-text>
                        </v-col>
                        <v-col cols="4" xs="4">
                            <v-card-text class="text-center green--text">${{total}}</v-card-text>
                        </v-col>
                        <v-col cols="4" xs="4">
                            <v-row class="justify-center align-center">
                                <v-btn fab color="success" @click="buy" v-if="$vuetify.breakpoint.mdAndUp">
                                    <v-icon>paid</v-icon>
                                </v-btn>
                                <v-btn block color="success" @click="buy" v-else>
                                    COMPRAR
                                </v-btn>
                            </v-row>
                        </v-col>
                    </v-row>
                </v-card>
            </v-col>
            <v-col cols="12" sm="12" v-if="cart.length>0">
                <v-form v-model="formValue">
                    <v-card class="pa-4 my-10" v-for="(product, index) in cart" v-bind:key="product.title">
                        <v-row class="align-center" >
                            <v-col cols="12" sm="12" md="1" lg="1">
                            <v-row justify="center">
                                <v-img :src="product.image" max-width="400" aspect-ratio="1.2"></v-img>
                            </v-row>
                            </v-col>
                            <v-col cols="6" sm="6" md="2" lg="2">
                                <div class="text-center text-h6">
                                    {{product.title}}
                                </div>
                            </v-col>
                            <v-col cols="6" sm="6" md="2" lg="2">
                                <div class="text-center text-h5 green--text">${{product.price}}</div>
                            </v-col>
                            <v-col cols="12" sm="12" md="2" lg="2">
                                <div class="text-left text-subtitle-1">{{product.description}}</div>
                            </v-col>
                            <v-col cols="6" sm="6" md="2" lg="2" v-if="$vuetify.breakpoint.mdAndUp">
                                <v-card-text class="text-center">{{product.category}}</v-card-text>
                            </v-col>
                            <v-col cols="12" sm="12" md="2" lg="2">
                                <v-text-field class="mx-auto" type="number" min="1" :rules="unitRule" v-model="product.units"></v-text-field>
                            </v-col>
                            <v-col cols="12" sm="12" md="1" lg="1">
                                <v-row class="justify-center align-center">
                                    <v-btn v-if="$vuetify.breakpoint.mdAndUp" fab color="red" @click="deletePetition(index)">
                                        <v-icon>delete</v-icon>
                                    </v-btn>
                                    <v-btn v-else block color="red" @click="deletePetition(index)">
                                        <v-icon>delete</v-icon>
                                    </v-btn>
                                </v-row>
                            </v-col>
                        </v-row>
                    </v-card>
                </v-form>
            </v-col>
        </v-row>
        <v-row v-if="$vuetify.breakpoint.mdAndUp && formValue" class="mx-4">
            <v-col col="12" sm="12">
              <v-card class="pa-4" v-if="cart.length>0">
                  <v-row class="align-center">
                      <v-col col="12" sm="12" md="12" lg="2">
                          <v-card-text class="text-center">TOTAL</v-card-text>
                      </v-col>
                      <v-col col="12" sm="12" md="12" lg="1" offset-lg="2">
                          <v-card-text class="text-center green--text">${{total}}</v-card-text>
                      </v-col>
                      <v-col col="12" offset-sm="6" sm="1">
                          <v-row class="justify-center align-center">
                              <v-btn fab color="success" @click="buy" v-if="display =='lg' || display == 'xl'">
                                  <v-icon>paid</v-icon>
                              </v-btn>
                              <v-btn block color="success" @click="buy" v-else>
                                  COMPRAR
                              </v-btn>
                          </v-row>
                      </v-col>
                  </v-row>
              </v-card>
          </v-col>
        </v-row>
        <v-dialog v-model="dialog" width="50%">
            <v-card>
        <v-card-title class="justify-center">
          ¿Está seguro que quiere eliminar el producto de su carrito?
        </v-card-title>

        <v-divider></v-divider>

        <v-card-actions>
          <v-btn
            color="primary"
            @click="dialog = !dialog"
            width="40%"
          >
            NO
          </v-btn>
          <v-spacer></v-spacer>
          <v-btn
            color="error"
            @click="deleteProduct"
            width="40%"
            :loading="deleteLoading"
          >
            SI
          </v-btn>
        </v-card-actions>
      </v-card>
        </v-dialog>
    </div> 
</template>

<script>
import { mapMutations } from 'vuex'
export default {
    name: 'CartView',
    data(){
        return{
            formValue: false,
            deleteLoading: false,
            index: null,
            dialog: false,
            unitRule: [
                value => !!value || 'Este campo no puede estar vacío',
                value => value > 0 || 'Cantidad incorrecta'
            ]
        }
    },
    computed:{
        cart(){
            return this.$store.state.cartModule.cart
        },
        total(){
            let total = 0
            this.cart.forEach(e=>{
                total += Number(e.price) * e.units
            })
            return total
        },
        display(){
            return this.$vuetify.breakpoint.name
        }
    },
    methods:{
        ...mapMutations(['deleteShop']),
        buy(){
            let msg = 'Hola! quería comprar los siguientes productos: %0A'
            this.cart.forEach( e => {
                msg += '- ' + e.units + ' unidades de ' + e.title + '%0A'
            })
            window.open(`whatsapp://send?text=${msg}&phone=+5491153326667`)
        },
        deletePetition(index){
            console.log(index)
            this.index = index
            this.dialog = true
        },
        deleteProduct(){
            this.deleteLoading = true
            this.deleteShop(this.index)
            this.deleteLoading = false
            this.dialog = false
        }
    }
}
</script>

